import {
  HttpClient,
  HttpErrorResponse,
  HttpRequest,
} from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppConstants } from "app/config/app.constant";
import { environment } from "environments/environment";
import { BehaviorSubject, throwError } from "rxjs";
import { catchError, tap } from "rxjs/operators";
// export const API_URL1 = 'http://localhost:1212';
import { ShowMessageService } from "./showMessage/show-message.service";
export const API_URL = AppConstants.baseURL;

@Injectable({
  providedIn: "root",
})
export class ApiService {
  private newUser = new BehaviorSubject<any>({
    prefix: "",
    firstName: "",
    lastName: "",
    primaryEmailAdress: "",
    phoneNumber: "",
  });

  constructor(
    private http: HttpClient,
    private showMessageService: ShowMessageService
  ) {}
  //   getOtp(otp:Object) {
  //     return this.http.post<any>(`${API_URL}/rest/opt/generateOtp`,otp);
  //   }
  //   getDropDownValues(entityName:string){
  //     return this.http.get<string[]>(`${API_URL}/rest/data/getMasterData?entityName=${entityName}`)

  // }
  //Error Handler
  private errorHandler = (errorResp: HttpErrorResponse) => {
    console.error("Error : " + errorResp.message);
    console.error("status : " + errorResp.status);
    this.showMessageService.errorMessage(errorResp.status, errorResp);
    return throwError(errorResp);
  };

  getOtp(data) {
    // return this.http.post<any>(`${API_URL}/rest/otp/generateOtp`,data).pipe(catchError(this.errorHandler));
    return this.http.post<any>(`${API_URL}/rest/otp/generateOtp`, data);
  }
  verifyOtp(data) {
    return this.http.put<any>(`${API_URL}/rest/otp/validateOtp`, data);
    // return this.http.put<any>(`${API_URL}/rest/otp/validateOtp`,data).pipe(catchError(this.errorHandler));
  }

  saveofCashWithdraw(data) {
    return this.http
      .post<any>(`${API_URL}/cash-withdrawal-service`, data)
      .pipe(catchError(this.errorHandler));
  }
  saveofChequeWithdraw(data) {
    // return this.http.post<any>(`${API_URL}/cheque-withdrawal`,data);
    return this.http
      .post<any>(`${API_URL}/cheque-withdrawal`, data)
      .pipe(catchError(this.errorHandler));
  }
  chequeDepositSave(data: object) {
    return this.http
      .post<any>(`${API_URL}/cheque-deposit/api`, data)
      .pipe(catchError(this.errorHandler));
  }
  gettingTransactionDetails(phoneNumber) {
    return this.http
      .get(`${API_URL}/customerdata/getPhoneNo/${phoneNumber}`, phoneNumber)
      .pipe(catchError(this.errorHandler));
  }
  generateToken(data) {
    return this.http
      .post(`${API_URL}/token/api`, data)
      .pipe(catchError(this.errorHandler));
  }
  getTokenEstimatedWaitingTime() {
    return this.http
      .get(`${API_URL}/token/api/generate-token-slips`)
      .pipe(catchError(this.errorHandler));
  }
  saveofChequeDeposit(data) {
    return this.http
      .post<any>(`${API_URL}/cheque-deposit/api`, data)
      .pipe(catchError(this.errorHandler));
  }
  saveofCashDeposit(data) {
    return this.http
      .post<any>(`${API_URL}/cash-deposit/api`, data)
      .pipe(catchError(this.errorHandler));
  }
  fetchBranchInfoByBranchName(branchName) {
    return this.http
      .get(
        `${API_URL}/branch/fetchBranchInfoByBranchName?branchName=${branchName}`
      )
      .pipe(catchError(this.errorHandler));
    // http://192.168.0.14:8081/Icust-Digital-Banking/branch/fetchBranchInfoByBranchName?branchName=Koramangala
  }
  saveofCashwithdraw(data) {
    return this.http
      .post<any>(`${API_URL}/cash-deposit/api`, data)
      .pipe(catchError(this.errorHandler));
  }
  fetchingQRcodeforToken(accountId) {
    return this.http
      .get(`${API_URL}/token/api/fetch-qr-code/${accountId}`, accountId)
      .pipe(catchError(this.errorHandler));
  }
  uploadingQRcodeforToken(data) {
    const payload: any = new FormData();
    payload.append("file", data);
    return this.http
      .post<any>(`${API_URL}/token/api/qr-code-reader`, payload)
      .pipe(catchError(this.errorHandler));
  }
  getCountryValues() {
    return this.http
      .get(`${API_URL}/currency`)
      .pipe(catchError(this.errorHandler));
  }

  fetchByaccNum(accNum: any) {
    return this.http
      .get<any>(`${API_URL}/accountdata/accountDetails/${accNum}`)
      .pipe(catchError(this.errorHandler));
  }
  fetchByaccDeatilsoncustId(cusId: any) {
    return this.http
      .get<any>(
        `${API_URL}/customerdata/fetchCustomerDetails?customerId=${cusId}`
      )
      .pipe(catchError(this.errorHandler));
  }
  getAppointmentId(appointmentId: any) {
    return this.http
      .get<any>(
        `${API_URL}/cash-deposit/api/fetchTransactionInfoByAppointmentId?appointmentId=${appointmentId}`
      )
  }
  fetchExchangeRateForCurrency(currency1: any,currency2:any) {
    return this.http
      .get<any>(
        `${API_URL}/currency-api/fetchExchangeRateForCurrency?currency1=${currency1}&currency2=${currency2}`
      )
      .pipe(catchError(this.errorHandler));
  }
  fetchBankInfoByBranchCode(branchCode: any) {
    return this.http
      .get<any>(
        `${API_URL}/bank/fetchBankInfoByBranchCode?branchCode=${branchCode}`
      )
      .pipe(catchError(this.errorHandler));
  }
  fetchChargeDetails(accountId: any) {
    return this.http
      .get<any>(
        `${API_URL}/accountdata/getChargeDetails?accountId=${accountId}`
      )
      .pipe(catchError(this.errorHandler));
  }

  searchNames(name: any) {
    return this.http
      .get(`${API_URL}/customerdata/fetchCustomers/${name}`)
      .pipe(catchError(this.errorHandler));
  }
  getCustomerByCustomerID(key, value) {
    return this.http.get<any>(
      `${API_URL}/customerdata/fetchCustomers?${key}=${value}`
    );
  }
  getBranchforKiosk() {
    return this.http
      .get<any>(`${API_URL}/branch`)
      .pipe(catchError(this.errorHandler));
  }
  getTransactionBranch(account_branch: string) {
    return this.http
      .get<any>(
        `${API_URL}/rest/data/getMasterData?entityName=${account_branch}`
      )
      .pipe(catchError(this.errorHandler));
  }

  storeFeedbackInfo(data: any) {
    return this.http
      .post<any>(`${API_URL}/customerdata/storeFeedbackInfo`, data)
      .pipe(catchError(this.errorHandler));
  }

  fetchKioskSystemDetails() {
    return this.http
    .get<any>(`${API_URL}/kioskSystem/fetchKioskSystemDetails`)
      .pipe(catchError(this.errorHandler));
  }

  goToLogin(data: any) {
    return this.http
    .post<any>(`${API_URL}/kioskSystem/login`, data)
      .pipe(catchError(this.errorHandler));
  }
  generateOtp(data: any) {
    return this.http
    .post<any>(`${API_URL}/kioskSystem/generateOtp`, data)
      .pipe(catchError(this.errorHandler));
  }

  fetchMobileOrEmail(sysCode: any) {
    return this.http
    .get<any>(`${API_URL}/kioskSystem/fetchMobileOrEmail?kioskSystemCode=${sysCode}`)
      .pipe(catchError(this.errorHandler));
  }
  
}
