import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { MatDialogRef } from "@angular/material/dialog";
import { DialogService } from "app/shared/services/Dialog/dialog.service";
import { FormControl } from "@angular/forms";
// import { ApiService } from 'src/app/services/api.service';
import { Location } from "@angular/common";
import { ApiService } from "app/shared/services/api.service";

@Component({
  selector: "app-feedback",
  templateUrl: "./feedback.component.html",
  styleUrls: ["./feedback.component.scss"],
})
export class FeedbackComponent implements OnInit {
  titleMessage = "Thank you for choosing DemoBank for your banking needs";
  phoneNumber: string;
  rating = new FormControl();
  // message = new FormControl();
  routerData: any;
  ratingOfCustomer: any;

  constructor(
    private router: Router,
    public dialogRef: MatDialogRef<FeedbackComponent>,
    private dialogService: DialogService,
    private location: Location,
    private api: ApiService
  ) {}

  ngOnInit(): void {
    if (this.router.getCurrentNavigation() != null) {
      this.routerData = this.router.getCurrentNavigation().extras.state;
    }
  }

  handleChange(event) {
    console.log(event.value);
    this.ratingOfCustomer = event.value;
  }

  // goToClose() {
  //   this.dialogRef.close();
  //   this.router.navigateByUrl("/others/kioskHome");
  // }
  onSubmit() {
    console.log(this.ratingOfCustomer);

    const feedback = {
      customerId: localStorage.getItem("customerId") || "",
      feedback: this.ratingOfCustomer,
      // suggestion: this.message.value
    };
    console.log(feedback);
    this.api.storeFeedbackInfo(feedback).subscribe(
      (resp) => {
        console.log(resp);
        this.onSkip();
      },
      (err) => {
        console.log("ERROR: ", err);
        // this.openToast('unable to save feedback')
      }
    );
  }

  onSkip() {
    this.dialogRef.close();
    if (this.routerData?.goBack) {
      this.location.back();
    } else {
      localStorage.clear();
      sessionStorage.clear();
      // this.router.navigate(['login'], { replaceUrl: true });
      this.router.navigateByUrl("/sessions/login");
    }
  }
}
