import { Country, MatSelectCountryComponent } from '@angular-material-extensions/select-country';
import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { DialogService } from 'app/shared/services/Dialog/dialog.service';

interface Testing {
  value: string;
  viewValue: string;
}


@Component({
  selector: 'app-operations',
  templateUrl: './operations.component.html',
  styleUrls: ['./operations.component.scss']
})
export class OperationsComponent implements OnInit {
  operationsForm: FormGroup;
  @Input() title : any;
  operation : string;
  countryFormControl = new FormControl();
  defaultValue: Country = {
    name: 'Deutschland',
    alpha2Code: 'DE',
    alpha3Code: 'DEU',
    numericCode: '276',
    callingCode: '+49'
  };
  constructor(private router : Router,private formBuilder: FormBuilder, private dialogService : DialogService) { }
  ngOnInit(): void {
    console.log(this.title);
    this.operationsForm = this.formBuilder.group({
      country: [
        {
          value: {
            name: 'Deutschland',
            alpha2Code: 'DE',
            alpha3Code: 'DEU',
            numericCode: '276',
            callingCode: '+49'
          },
          disabled: false
        }
      ]
    });

    this.operationsForm
      .get('country')
      .valueChanges.subscribe((country) =>
      console.log(
        'this.countryFormGroup.get(\'country\').valueChanges',
        country
      )
    );

    this.countryFormControl.valueChanges.subscribe((country) =>
      console.log('this.countryFormControl.valueChanges', country)
    );

  }


  testing: Testing[] = [
    { value: 'Demo', viewValue: 'Demo 1' },
    { value: 'Demo', viewValue: 'Demo 2' },
    { value: 'Demo', viewValue: 'Demo 3' }
  ];


  onCountrySelected($event: Country): void {
    console.log($event);
  }

  goToServiceScreens(){
    this.router.navigateByUrl('/others/kioskHome');
  }
  
  goToAuthenticate(){
    this.dialogService.authenticationDialog();
  }
}

