import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { MatDialogRef } from "@angular/material/dialog";
import { DialogService } from "app/shared/services/Dialog/dialog.service";

@Component({
  selector: "app-exit-screen",
  templateUrl: "./exit-screen.component.html",
  styleUrls: ["./exit-screen.component.scss"],
})
export class ExitScreenComponent implements OnInit {
  titleMessage = "Are you sure you want to Exit the Screen ?";
  phoneNumber: string;

  constructor(
    private router: Router,
    public dialogRef: MatDialogRef<ExitScreenComponent>,private dialogService: DialogService
  ) {}

  ngOnInit(): void {}
  goToClose() {
    this.dialogRef.close();
    // this.router.navigateByUrl("/others/kioskHome");
  }
  feedBack() {
    this.dialogRef.close();
    this.dialogService.feedBack();
  }
}
