import { Injectable } from '@angular/core';
import { BehaviorSubject, EMPTY } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  private paramSource = new BehaviorSubject({});
  getNavParam = this.paramSource.asObservable();

  
  private searchItem = new BehaviorSubject({});
  getSearchItem = this.searchItem.asObservable();

  private sendCustomerId = new BehaviorSubject("");
  getCustomerId = this.sendCustomerId.asObservable();

  private accountFormData = new BehaviorSubject({});
  private transactionFormData = new BehaviorSubject({});
  private denominationFormData = new BehaviorSubject({});
  private forexTransTypeData = new BehaviorSubject({});
  getforexTransTypeData = this.forexTransTypeData.asObservable();

  getaccountFormdata = this.accountFormData.asObservable();
  gettransactionFormdata = this.transactionFormData.asObservable();
  getdenominationFormdata = this.denominationFormData.asObservable();
  constructor() {}

   sendNavParam(params: object) {
    this.paramSource.next(params)
  }
  sendNavforexTransType(params: string) {
    this.forexTransTypeData.next(params)
  }

  shareSearchItem(params: object) {
    this.searchItem.next(params)
  }

  shareCustomerId(customerId){
    this.sendCustomerId.next(customerId);
  }

  sendaccountFormData(params: object) {
    this.accountFormData.next(params)
  }
  sendtransactionFormData(params: object) {
    this.transactionFormData.next(params)
  }
  sendDenominationFormData(params: object) {
    this.denominationFormData.next(params)
  }

  removetransactionFormData(params:object){
    this.transactionFormData.next(EMPTY);
  }
  removedenominationFormData(params:object){
    this.denominationFormData.next(EMPTY);
  }
  removeaccountFormData(params:object){
    this.accountFormData.next(EMPTY);
  }
  removeNavParam(params:object){
    this.paramSource.next(EMPTY);
  }



}