import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';

@Component({
  selector: 'app-authenticate',
  templateUrl: './authenticate.component.html',
  styleUrls: ['./authenticate.component.scss']
})
export class AuthenticateComponent implements OnInit {

  titleMessage: string = "Please use your fingerprint to authenticate";
  contentMessage : string = "Please place your thumb on the fingerprint reader to proceed.";

  constructor(
    private router: Router,
    public dialogRef: MatDialogRef<AuthenticateComponent>
  ) { }

  ngOnInit() {
    
  }

  goToClose(){
    this.dialogRef.close(); 
  }
  goToScanner(){
    this.dialogRef.close();
    // this.router.navigateByUrl('/others/scanner');
  }




}
