import { MatDialog } from "@angular/material/dialog";
import { Inject, Injectable, Input } from "@angular/core";
import { Router } from "@angular/router";
import { DOCUMENT } from "@angular/common";
import { AuthenticateComponent } from "app/views/others/authenticate/authenticate.component";
import { TokenGenerationComponent } from "app/views/others/token-generation/token-generation.component";
import { ApiService } from "../api.service";
import { LogoutPopupComponent } from "app/views/sessions/logout-popup/logout-popup.component";
import { ExitScreenComponent } from "app/views/others/exit-screen/exit-screen.component";
import { FeedbackComponent } from "app/views/others/feedback/feedback.component";

export class TokenObjects {
  entityCode: string;
  bankCode: string;
  tokenBranch: string;
  accountNumber: number;
  customerCategory: string;
  productCode: string;
}

@Injectable({
  providedIn: "root",
})
export class DialogService {
  @Input() tokenResp: any;
  tokenObjects = new TokenObjects();
  constructor(public dialog: MatDialog, private apiService: ApiService) {}

  authenticationDialog(): void {
    console.log("test");
    const dialogRef = this.dialog.open(AuthenticateComponent, {
      width: "55%",
      height: "68%",
      backdropClass: "dialog-bg-trans",
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log("The dialog was closed");
    });
  }

  tokenGenerationDialog() {
    console.log(this.tokenResp, "Token Response");
    this.tokenObjects.entityCode = "entityCode1";
    const tokenDialog = this.dialog.open(TokenGenerationComponent, {
      data: {},
      width: "55%",
      height: "72%",
      backdropClass: "dialog-bg-trans",
      disableClose: true,
    });

    tokenDialog.afterClosed().subscribe((result) => {
      console.log("The dialog was closed");
    });
  }

  logoutDialog() {
    console.log("Logout Response");
    const dialogBox = this.dialog.open(LogoutPopupComponent, {
      data: {},
      width: "50%",
      height: "65%",
      backdropClass: "dialog-bg-trans",
      disableClose: true,
    });

    dialogBox.afterClosed().subscribe((result) => {
      console.log("The dialog was closed");
    });
  }

  clickExit() {
    console.log("exit Response");
    const dialogBox = this.dialog.open(ExitScreenComponent, {
      data: {},
      width: "50%",
      height: "65%",
      backdropClass: "dialog-bg-trans",
      disableClose: true,
    });

    dialogBox.afterClosed().subscribe((result) => {
      console.log("The dialog was closed");
    });
  }

  feedBack() {
    console.log("exit Response");
    const dialogBox = this.dialog.open(FeedbackComponent, {
      data: {},
      width: "50%",
      height: "65%",
      backdropClass: "dialog-bg-trans",
      disableClose: true,
    });

    dialogBox.afterClosed().subscribe((result) => {
      console.log("The dialog was closed");
    });
  }
}
