import { Component, OnInit, Input, OnDestroy, Renderer2 } from '@angular/core';
import { NavigationService } from "../../../shared/services/navigation.service";
import { Subscription } from 'rxjs';
import { ThemeService } from '../../../shared/services/theme.service';
import { TranslateService } from '@ngx-translate/core';
import { LayoutService } from '../../services/layout.service';
import { JwtAuthService } from 'app/shared/services/auth/jwt-auth.service';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { CommonService } from 'app/shared/services/common.service';
import { MatDialog } from '@angular/material/dialog';
import { SearchComponent } from 'app/views/others/search/search.component';

@Component({
  selector: 'app-header-top',
  templateUrl: './header-top.component.html',
  styleUrls: ['./header-top.component.scss'],
})
export class HeaderTopComponent implements OnInit, OnDestroy {
  layoutConf: any;
  menuItems:any;
  menuItemSub: Subscription;
  rumangoThemes: any[] = [];
  currentLang = 'en';
  availableLangs = [{
    name: 'English',
    code: 'en',
  }, {
    name: 'Spanish',
    code: 'es',
  }]
  @Input() notificPanel;
  phoneNumber: string;
  fullname: any;
  messageReceived: any;
  private subscriptionName: Subscription; //important to create a subscription
  notificationCount: any;
  constructor(
    private layout: LayoutService,
    private navService: NavigationService,
    public themeService: ThemeService,
    public translate: TranslateService,
    private renderer: Renderer2,
    public jwtAuth: JwtAuthService,
    private apiService:ApiService,
    private ls: LocalStoreService,
    private commonService:CommonService,
    private dialog: MatDialog,

  ) { 
    this.subscriptionName= this.commonService.getNotificationUpdate().subscribe
    (message => { //message contains the data sent from service
      console.log('message :: ',message);
      this.notificationCount=message.noOfMessages
    });
  }

  ngOnInit() {
    if(this.phoneNumber !== null){
    this.gettingallTansactionDetails(this.phoneNumber);
    }
    else {
      this.fullname = localStorage.getItem("custholderName");
    }
    this.layoutConf = this.layout.layoutConf;
    this.rumangoThemes = this.themeService.rumangoThemes;
    this.menuItemSub = this.navService.menuItems$
    .subscribe(res => {
      res = res.filter(item => item.type !== 'icon' && item.type !== 'separator');
      let limit = 4
      let mainItems:any[] = res.slice(0, limit)
      if(res.length <= limit) {
        return this.menuItems = mainItems
      }
      let subItems:any[] = res.slice(limit, res.length - 1)
      mainItems.push({
        name: 'More',
        type: 'dropDown',
        tooltip: 'More',
        icon: 'more_horiz',
        sub: subItems
      })
      this.menuItems = mainItems
    })
  }

 
    
      
    
       
  ngOnDestroy() {
    this.menuItemSub.unsubscribe()
    this.subscriptionName.unsubscribe();
  }
  setLang() {
    this.translate.use(this.currentLang)
  }
  changeTheme(theme) {
    this.layout.publishLayoutChange({matTheme: theme.name})
  }
  toggleNotific() {
    this.notificPanel.toggle();
  }
  toggleSidenav() {
    if(this.layoutConf.sidebarStyle === 'closed') {
      return this.layout.publishLayoutChange({
        sidebarStyle: 'full'
      })
    }
    this.layout.publishLayoutChange({
      sidebarStyle: 'closed'
    })
  }
  //search implementation
  onSearch(){
    const dialogRef = this.dialog.open(SearchComponent,{
      width: '720px',
      disableClose: true,
      panelClass:"custom",
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }
  gettingallTansactionDetails(phoneNumber) {
    this.phoneNumber = localStorage.getItem("phonenumFromLogin");
    this.apiService.gettingTransactionDetails(this.phoneNumber).subscribe((allTransactionResp : any)=> {
    this.ls.setItem("customerId",allTransactionResp.customerId);
    console.log("All Transaction Details in Header",allTransactionResp, allTransactionResp.firstName + ' ' + allTransactionResp.firstName);
    this.fullname = (allTransactionResp.firstName ? allTransactionResp.firstName + ' ' : ' ') + 
    (allTransactionResp.middleName ? allTransactionResp.middleName + ' ' : ' ') +
     (allTransactionResp.lastName ? allTransactionResp.lastName : ' ');
      })
    }

}
