@Injectable({
  providedIn: 'root'
})
export class TokenService {
  // private baseUrl = "http://localhost:1212";

  private newUser = new BehaviorSubject<any>({
    prefix: '',
    firstName: '',
    lastName: '',
    primaryEmailAdress: '',
    phoneNumber: ''
  });
  private verify= new BehaviorSubject<any>({
    verify:'',
    type:''
  })
  private Index= new BehaviorSubject<any>({
    index:'',
  })
  private Id= new BehaviorSubject<any>({
    id:'',
  })
  getverify() {
    return this.verify.asObservable();
  }
  setVerify(verify: any) {
    this.verify.next(verify);
  }
  getId() {
    return this.Id.asObservable();
  }
  setId(id: any) {
    this.Id.next(id);
  }
  setIndex(index: any) {
    this.Index.next(index);
  }

  getIndex() {
    return this.Index.asObservable();
  }
  setNewUserInfo(user: any) {
    this.newUser.next(user);
  }

  getNewUserInfo() {
    return this.newUser.asObservable();
  }
  constructor(private httpClient: HttpClient, private showMessageService: ShowMessageService) { }

  //Error Handler

  private errorHandler(error: HttpErrorResponse) {
    if (error.status === 0) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', error.error);
      this.showMessageService.errorMessage('An Error Occured', error.error);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong.
      console.error(
        `Backend returned code ${error.status}, body was: `, error.error);
      this.showMessageService.errorMessage(`${error.status}`, error.error);
    }
    // Return an observable with a user-facing error message.
    return throwError(
      'Something bad happened; please try again later.');
  }
// new changes
}
