import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CommonService {

  constructor() { }

  private subjectName = new Subject<any>(); //need to create a subject
    
  sendNotificationUpdate(message: any, messageCount:number) { //the component that wants to update something, calls this fn
      this.subjectName.next({ text: message ,noOfMessages:messageCount}); //next() will feed the value in Subject
  }

  getNotificationUpdate(): Observable<any> { //the receiver component calls this function 
      return this.subjectName.asObservable(); //it returns as an observable to which the receiver funtion will subscribe
  }
}
